import React from "react";

const Project = (props) => {
    const project = props.project;
    const url = props.project.url;
    return <div>
        <div style={styles.title}><u>{project.title}</u></div>
        <div style={styles.content}>
            <div style={styles.item}><i>{project.duration}</i></div>
            <div style={styles.item}>{project.content}</div>
            <div style={styles.item}>{project.language}</div>
            {url ? <div style={styles.item}>URL: <a href={url}>{url}</a></div> : null}
        </div>
    </div>
};

export default Project;

